import './App.css';
import Home from './components/Home/Home'

import React from "react";
import {
  HashRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";


export default function App() {
  return (
    <Router>
    <div>
      <nav>
        <ul>
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="/about">About</Link>
          </li>
          <li>
            <Link to="/users">Users</Link>
          </li>
        </ul>
      </nav>

      {/* A <Switch> looks through its children <Route>s and
          renders the first one that matches the current URL. */}
      <Switch>
        <Route path="/about">
          <About/>
        </Route>
        <Route path="/users">
          <Users />
        </Route>
        <Route path="/apollo">
          <Apollo/>
        </Route>
        <Route path="/">
          <Home />
        </Route>
      </Switch>
    </div>
  </Router>
  );

  function About() {
    return <h2>About</h2>;
  }

  function Apollo() {
    return <h2>Apollo</h2>;
  }
  
  function Users() {
    return <h2>Axiom</h2>;
  }
}